/**
 * 
 *
 * @param {Array} array .
 * @param {Number} size .
 * @returns {Array}  .
 */
export function chunk(array, size) {
  var length = array == null ? 0 : array.length;
  if (!length || size < 1) {
    return [];
  }
  var index = 0,
      resIndex = 0,
      result = Array(Math.ceil(length / size));

  while (index < length) {
    result[resIndex++] = array.slice(index, (index += size));
  }
  return result;
}

/**
 * predicate 
 *
 * @param {Array} array .
 * @param {Function} predicate .
 * @param {boolean} [isDrop] .
 * @param {boolean} [fromRight] .
 * @returns {Array} .
 */
export function drop(array, predicate, isDrop=true, fromRight=false) {
  var length = array.length,
      index = fromRight ? length : -1;
  while ((fromRight ? index-- : ++index < length) &&
    predicate(array[index], index, array)) {}
  return isDrop
    ? array.slice((fromRight ? 0 : index), (fromRight ? index + 1 : length))
    : array.slice((fromRight ? index + 1 : 0), (fromRight ? length : index));
}


